import axios from "axios";
export default {
  computed: {
    user: function user() {
      return this.$store.state.user;
    },
    apiURL: function apiURL() {
      return this.$store.state.apiURL;
    }
  },
  data: function data() {
    return {
      snack: false,
      snackColor: "",
      snackText: "",
      valid: false
    };
  },
  methods: {
    updateUser: function updateUser() {
      var _this = this;

      axios.post(this.$store.state.apiURL + "/user/update-user/" + this.user._id, {
        user: this.user,
        headers: {
          token: localStorage.getItem("token")
        }
      }).then(function (res) {
        console.log(res.data);
        _this.snack = true;
        _this.snackText = "User Profile successfully updated!";
        _this.snackColor = "green";
      });
    }
  }
};